function Home() {
  return (
    <form
      className="fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 min-w-96 aspect-square
            border rounded-lg p-2
            flex flex-col justify-evenly items-center
        "
    >
      <h1 className="text-xl font-semibold">Space Invaders</h1>
      <label htmlFor="name">
        <span className="font-semibold text-sm">Name</span>
        <input
          type="text"
          name="name"
          id="name"
          className=" input"
          placeholder="Enter your name"
        />
      </label>
      <div className="flex gap-x-4">
        <button type="submit" className="btn btn-primary">
          Start
        </button>
        <button type="submit" className="btn btn-secondary">
          Join
        </button>
      </div>
    </form>
  );
}
export default Home;
